import React from 'react';
import Grid from '@mui/material/Grid';
// react-icons
import CredentialTabs from '../../tabs/CredentialTabs';
import UserCredentialsDetailsPage from './UserCredentialsDetailsPage';

export default function UserCredential(props) {

    return (
        <div style={{ height: '100%', width: '100%' }}>
            <Grid container spacing={1}>
                <Grid item xs={12}>
                    <UserCredentialsDetailsPage />
                </Grid>
                <Grid item xs={12}>
                    <CredentialTabs />
                </Grid>
            </Grid>
        </div>
    )
}
