import React from 'react'
// Modal Components
import LargeModalDialog from '../shared/Dialog/LargeModalDialog';
import NewDetailsBusinessForm from '../forms/NewBusinessDetailsForm';

export default function BusinessModal(props) {
    return (
        <div>
            <LargeModalDialog open={props.open} title={`New Business Details`} toggle={props.toggle} size="md">
                <NewDetailsBusinessForm toggle={props.toggle} loadNewData={(data) => props.loadNewData(data)} />
            </LargeModalDialog>
        </div>
    )
}
