import React from 'react'
import Dropzone from 'react-dropzone'
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import { FaTimes } from 'react-icons/fa';
import { Typography } from '@mui/material';
import toastService from '../../../../services/toast/toastService';

export default function FileDropzone(props) {

    let maxFiles = props.maxFiles ?? 5;
    const [files, setFiles] = React.useState(props.filesPreview ?? []);

    const droppedFiles = (dropped) => {  
        
        if (dropped.length <= maxFiles) {
            if (files.length < maxFiles) {
                let formatted = dropped.map(file => {
                    return {
                        path: URL.createObjectURL(file)
                    }
                });

                let newFiles = [...files, ...formatted];

                setFiles(newFiles);

                if (props.drop) {
                    props.drop(dropped);
                }

                if (props.preview) {
                    props.preview(newFiles);
                }
            }
        }else{
            toastService.error(`Only ${maxFiles} ${maxFiles <= 1 ? "file" : "files"} is allowed`);
        }
    }

    const handleRemoveFile = (e, selectedIndex) => {

        e.stopPropagation();

        let filtered = files.filter((data, index) => {
            return index !== selectedIndex;
        })

        let oldfiles = props.files;

        let filteredDropped = oldfiles.filter((data, index) => {
            return index !== selectedIndex;
        })

        setFiles(filtered);

        if (props.drop) {
            props.drop(filteredDropped);
        }

        if (props.preview) {
            props.preview(filtered);
        }
    }

    const handleClear = () => {

        setFiles([]);

        if (props.drop) {
            props.drop([]);
        }
    }

    return (
        <div>
            {files.length > 0 && (props.allowClear ?? true) && 
                <Button
                    sx={{
                        position: 'absolute',
                        "&:hover": { backgroundColor: 'transparent' },
                    }}
                    onClick={() => handleClear()}
                    size="small"
                    color="secondary"
                    disableRipple
                >
                    Clear
                </Button>
            }
            <Dropzone onDrop={files => droppedFiles(files)} accept="image/*" disabled={props.disable ?? false}>
                {({ getRootProps, getInputProps }) => (
                    <section>
                        <div {...getRootProps()}
                            style={
                                {
                                    border: '4px dashed #eee',
                                    textAlign: "center",
                                    height: props.height ?? '100%',
                                    width: props.width ?? '100%',
                                    padding: 5,
                                    display: "flex",
                                    flexDirection: "row",
                                    justifyContent: "center",
                                    alignItems: "flex-start"
                                }
                            }>
                            <input {...getInputProps()} />
                            {files.length <= 0 &&
                                <div>
                                    <Typography sx={{ mt: props.labelMargin ?? 10, "&:hover": { cursor: 'pointer' } }}>{props.title}</Typography>
                                    <Typography variant="caption">Maximum of {maxFiles} {maxFiles > 1 ? 'files' : 'file'}</Typography>
                                </div>
                            }
                            {files.map((file, index) => {
                                return (
                                    <Box
                                        display="flex"
                                        flexDirection="column"
                                        justifyContent="center"
                                        sx={{
                                            width: 150,
                                            m: 1,
                                            mt: 1
                                        }}
                                        key={`key-${index}`}
                                    >
                                        <Button
                                            sx={{ 
                                                ml: -2,
                                                "&:hover": { backgroundColor: 'transparent' },  
                                            }}
                                            size="small"
                                            onClick={(e) => handleRemoveFile(e, index)}
                                            disableRipple
                                            color="secondary"
                                            disabled={props.loading ?? false}
                                        >
                                            <FaTimes style={{ marginRight: 3 }} /> Remove
                                        </Button>
                                        <img src={file.path}
                                            width="140"
                                            height="130"
                                            alt={`file-${index}`}
                                            style={{ marginTop: 5 }}
                                        />
                                    </Box>
                                );
                            })}
                        </div>
                    </section>
                )}
            </Dropzone>
        </div>
    )
}
