import React from 'react'
import { parseISO, format } from 'date-fns';
import utilityService from '../../../services/utils/utilityServices';
import toastService from '../../../services/toast/toastService';
import employeeRepository from '../../../repositories/employeeRepository';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import LoadingButton from '@mui/lab/LoadingButton';
import Paper from '@mui/material/Paper';
// Form Components
import Gender from './fields/Gender';
import CivilStatus from './fields/CivilStatus';
import Birthdate from './fields/Birthdate';
import ContactNumber from './fields/ContactNumber';
// react-icons
import { FaSave } from 'react-icons/fa';

export default function EmployeeProfileForm(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const me = authRedux?.me;
    const employee = props?.selectedEmployee?.data;

    let profile = employee;

    if (authRedux?.role.id < 5) {
        profile = me?.employee;
    } 

    let bday = null;
    let mygender = "";
    let mycontactnum = "";

    if (profile?.birthdate !== undefined) {
        bday = format(new Date(profile?.birthdate), "MMMM dd, yyyy");
    }
    if (profile?.gender !== undefined) {
        mygender = profile?.gender.toLowerCase() === "m" ? "Male" : "Female";
    }

    if (profile?.contact_no !== undefined) {
        mycontactnum = profile?.contact_no?.replace("+63", "") ?? "";
    }

    const [isEdit, setIsEdit] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    // Basic Profile  
    const [employeeID, setEmployeeID] = React.useState(profile?.employee_id);
    const [lastName, setLastName] = React.useState(profile?.last_name ?? "");
    const [firstName, setFirstName] = React.useState(profile?.first_name ?? "");
    const [middleName, setMiddleName] = React.useState(profile?.middle_name ?? "");
    const [suffix, setSuffix] = React.useState(profile?.suffix ?? "");
    const [birthdate, setBirthdate] = React.useState(bday);
    const [genderValue, setGenderValue] = React.useState(profile?.gender);
    const [civilStatus, setCivilStatus] = React.useState(utilityService.capitalizeFirstLetter(profile?.civil_status) ?? "");
    const [contactNumber, setContactNumber] = React.useState(mycontactnum);

    const handleSubmit = async (e) => {
        try {
            e.preventDefault();

            setLoading(true);

            let data = {
                employeeId: employeeID,
                firstName: firstName,
                lastName: lastName,
                middleName: middleName,
                suffix: suffix,
                birthdate: new Date(birthdate),
                gender: genderValue,
                civilStatus: civilStatus.toLowerCase(),
                contactNumber: contactNumber
            }

            let result = await employeeRepository.update(data);

            if (!result.error) {
                await dispatch(getMe()).unwrap();
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setIsEdit(false);
            setLoading(false);
        }
    }

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <div>
                <Paper square sx={{ p: 3.5, pt: 1, m: 1 }}>
                    <Grid container spacing={2}>
                        <Grid item xs={12} sx={{ mb: 1, mt: 2 }}>
                            <FormControlLabel sx={{ mr: 1 }} control={<Switch checked={isEdit} disabled={loading} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />} label="Edit" />
                            {isEdit &&
                                <React.Fragment>
                                    <LoadingButton
                                        loading={loading}
                                        sx={{
                                            pt: 0,
                                            pb: 0,
                                            width: 75,
                                            '&:hover': {
                                                backgroundColor: 'transparent'
                                            },
                                            '&:before': {
                                                content: "'|'",
                                                ml: -1,
                                                mr: 1,
                                                color: '#000'
                                            },
                                            fontSize: 14,
                                            color: '#1976D2'
                                        }}
                                        type="submit"
                                        fullWidth
                                        variant="text"
                                        color="primary"
                                        onClick={handleSubmit}
                                    >
                                        <FaSave size={14} style={{ marginRight: 5 }} />
                                        Save
                                    </LoadingButton>
                                </React.Fragment>
                            }
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setLastName(e.target.value)}
                                value={lastName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="lastName"
                                label="Last Name"
                                placeholder="Last Name"
                                name="lastName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setFirstName(e.target.value)}
                                value={firstName}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="firstName"
                                label="First Name"
                                placeholder="First Name"
                                name="firstName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setMiddleName(e.target.value)}
                                value={middleName}
                                variant="standard"
                                size="small"
                                fullWidth
                                id="middleName"
                                label="Middle Name"
                                placeholder="Middle Name"
                                name="middleName"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setSuffix(e.target.value)}
                                value={suffix}
                                variant="standard"
                                size="small"
                                fullWidth
                                id="suffix"
                                label="Name Suffix"
                                placeholder="Name Suffix"
                                name="suffix"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            <TextField
                                onChange={(e) => setEmployeeID(e.target.value)}
                                value={employeeID}
                                variant="standard"
                                size="small"
                                required={isEdit}
                                fullWidth
                                id="employeeID"
                                label="Employee ID"
                                placeholder="Employee ID"
                                name="employeeID"
                                inputProps={
                                    { readOnly: !isEdit }
                                }
                            />
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={birthdate ?? ""}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Birthdate"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={mygender}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Gender"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <Gender setGender={setGenderValue} gender={genderValue} variant="standard" isRequired={isEdit} />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={civilStatus}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Civil Status"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <CivilStatus setCivilStatus={setCivilStatus} civilStatus={civilStatus} variant="standard" />
                            }
                        </Grid>
                        <Grid item xs={4}>
                            {!isEdit &&
                                <TextField
                                    value={`+639${contactNumber}`}
                                    variant="standard"
                                    size="small"
                                    fullWidth
                                    label="Contact Number"
                                    inputProps={
                                        { readOnly: true }
                                    }
                                />
                            }
                            {isEdit &&
                                <TextField
                                    onChange={(e) => setContactNumber(e.target.value)}
                                    value={contactNumber}
                                    variant="standard"
                                    size="small"
                                    required
                                    fullWidth
                                    id="contactNumber"
                                    label="Contact Number"
                                    name="contactNumber"
                                    InputProps={{
                                        inputComponent: ContactNumber
                                    }}
                                />
                            }
                        </Grid>
                    </Grid>
                </Paper>
            </div>
        </form>
    )
}
