import * as React from 'react';
import config from '../../../../config/config';
import { useNavigate } from 'react-router-dom';
// redux
import { useSelector } from 'react-redux';
// MUI Components 
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
import Avatar from '@mui/material/Avatar';
import Button from '@mui/material/Button';
// Page Components
import TabPanel from '../../tabs/components/TabPanel2';
import UserProfileDetailsPage from './UserProfileDetailsPage';
import UserCredentialPage from './UserCredentialPage';
import ChangeAvatarModal from '../../modals/ChangeAvatarModal';
// react-icons
import { FaEdit, FaCogs, FaUserEdit, FaUserLock, FaUserCog } from 'react-icons/fa';
import UserSettingsPage from './UserSettingsPage';

export default function UserPage(props) {

    const navigate = useNavigate();

    const meRedux = useSelector((state) => state.auth.value).me;

    let avatar_path = null;

    if (meRedux?.avatar !== null) {
        avatar_path = `${config.RESOURCES}users/${meRedux?.uuid}/files/avatar/${meRedux?.avatar}`;
    }

    const options = [
        {
            name: 'Profile',
            icon: <FaUserEdit size={20} style={{ marginRight: 5 }} />,
            content: (props) => <UserProfileDetailsPage {...props} />,
        },
        {
            name: 'Credentials',
            icon: <FaUserLock size={20} style={{ marginRight: 5 }} />,
            content: (props) => <UserCredentialPage {...props} />,
        },
        {
            name: 'Settings',
            icon: <FaUserCog size={20} style={{ marginRight: 5 }} />,
            content: (props) => <UserSettingsPage {...props} />,
        },
    ];

    const [showChangeAvatarModal, setShowChangeAvatarModal] = React.useState(false);
    const toggleShowChangeAvatarModal = () => setShowChangeAvatarModal(!showChangeAvatarModal);

    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
        switch (newValue) {
            case 0: navigate(`/${props.user}/my/profile`); break;
            case 1: navigate(`/${props.user}/my/credentials`); break;
            case 2: navigate(`/${props.user}/my/settings`); break;
            default: return null;
        }
    };

    React.useEffect(() => {
        let mounted = false;

        if (!mounted) {
            setValue(props.index ?? 0);
        }

        return () => mounted = true;
    }, [props.index]);


    return (
        <Box
            sx={{ display: 'flex', flexGrow: 1, flexDirection: 'row', height: '100vh', width: '100vw', overflow: 'hidden' }}
        >
            {showChangeAvatarModal && <ChangeAvatarModal open={showChangeAvatarModal} toggle={toggleShowChangeAvatarModal} />}
            <Box>
                <Box display="flex" flexDirection="row" justifyContent="center" sx={{ p: 2, backgroundColor: '#eee' }}>
                    <FaCogs size={20} style={{ marginRight: 5, marginTop: 7 }} />
                    <Typography variant="h6" sx={{ mt: 0.2 }}>
                        My Settings
                    </Typography>
                </Box>
                <Box display="flex" flexDirection="column" alignItems="center" sx={{ p: 1, pt: 0, ml: 0, width: '100%', borderRight: '2px solid #eee', borderBottom: '1px solid #eee' }}>
                    <Avatar src={avatar_path} sx={{ width: 200, height: '100%', p: 1, mt: 1, mb: 1 }} variant="rounded" />
                    <Button
                        sx={{
                            mt: 1,
                            minWidth: 200,
                            borderRadius: 0,
                        }}
                        variant="text"
                        component="label"
                        size="small"
                        onClick={() => toggleShowChangeAvatarModal()}
                    >
                        <FaEdit size={11} style={{ marginRight: 5 }} />
                        Change Photo
                    </Button>
                </Box>
                <Tabs
                    orientation="vertical"
                    variant="scrollable"
                    value={value}
                    onChange={handleChange}
                    sx={{ height: '100vh', flexGrow: 1, width: 220, pt: 0, borderRight: '2px solid #eee' }}
                    TabIndicatorProps={{
                        style: {
                            right: 1,
                            height: 30,
                            marginTop: 10
                        }
                    }}
                >

                    {
                        options.map((data, index) => {
                            return <Tab onClick={() => handleChange(index)} sx={{ alignItems: "start" }} key={`tabkey-${index}`} label={
                                <Typography variant="subtitle2">
                                    <Box display="flex">
                                        {data.icon}
                                        {data.name}
                                    </Box>
                                </Typography>
                            } />
                        })
                    }
                </Tabs>
            </Box>
            <Box display="flex" flexGrow={1}>
                {
                    options.map((data, index) => {
                        return <TabPanel value={value} index={index} key={`tabpanelkey-${index}`}>
                            <Paper square elevation={0} sx={{ m: 1, height: 600, width: 'inherit' }}>
                                {data.content()}
                            </Paper>
                        </TabPanel>
                    })
                }
            </Box>
        </Box>
    );
}
