import React from 'react';
// Services
import toastService from '../../../services/toast/toastService';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
// Respository
import usersRepository from '../../../repositories/usersRepository';
// MUI Components
import Box from '@mui/material/Box';
import LoadingButton from '@mui/lab/LoadingButton';
// Page Components
import InnerDialog from '../shared/Dialog/InnerModalDialog';
import ProfilePhoto from '../shared/ProfilePhoto/ProfilePhoto';
// react-icons
import { FaUpload } from 'react-icons/fa';

export default function ChangeAvatarModal(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);

    const role = authRedux.role.id;
    const me = authRedux?.me;

    const [isPhotoSet, setIsPhotoSet] = React.useState(false);
    const [loading, setLoading] = React.useState(false);
    const [avatar, setAvatar] = React.useState(null);
    const [avatarPreview, setAvatarPreview] = React.useState(null);


    const handleChangeAvatar = async () => {
        try {
            setLoading(true);

            if (!avatar) {
                toastService.modalError("Please set your avatar");
                setLoading(false);
                return false;
            }

            let formData = new FormData();

            let firstName = authRedux?.me?.employee?.first_name ?? authRedux?.me?.user?.role?.role;
            let fileName = authRedux?.me?.avatar;

            if (role === 5) {
                firstName = authRedux?.me?.first_name;
                fileName = authRedux?.me?.user?.avatar
            }

            formData.append('firstName', firstName);
            formData.append('fileName', fileName);
            formData.append('avatar', avatar);

            let result = await usersRepository.changeAvatar({ formData });

            if (!result.error) {
                toastService.success(result.message);
                dispatch(getMe());
            }else{
                toastService.error(result.message);
            }

            setLoading(false);
        } catch (error) {
            setLoading(false);
            toastService.alert(error);
        }
    }

    return (
        <div>
            <InnerDialog open={props.open} toggle={props.toggle} title="Profile Photo">
                <Box display="flex" flexDirection="column" justifyContent="center" sx={{ minWidth: 400, p: 1 }}>
                    <Box sx={{ border: '1px solid  #eee', p: 4 }}>
                        <ProfilePhoto width={200} height={200} avatarPreview={avatarPreview} setAvatar={setAvatar} setAvatarPreview={setAvatarPreview} setIsPhotoSet={setIsPhotoSet} />
                    </Box>
                    {
                        isPhotoSet &&
                        <LoadingButton onClick={() => handleChangeAvatar()} variant='contained' sx={{ mt: 2 }} loading={loading}>
                            Upload Photo <FaUpload style={{ marginLeft: 5 }} />
                        </LoadingButton>
                    }
                </Box>
            </InnerDialog>
        </div>
    )
}
