import React from 'react';
// services
import toastService from '../../../services/toast/toastService';
// redux
import { useDispatch, useSelector } from 'react-redux';
import { me } from '../../../redux/async/authAsync';
// repository
import authRepository from '../../../repositories/authRepository';
// MUI Components  
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import InputAdornment from "@mui/material/InputAdornment";
import IconButton from "@mui/material/IconButton";
import LoadingButton from '@mui/lab/LoadingButton';
// react-icons
import { MdVisibility, MdVisibilityOff } from 'react-icons/md';
import { FaEdit } from 'react-icons/fa';

export default function ChangePasswordForm() {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);

    const [loading, setLoading] = React.useState(false);
    const [passwordnow, setPasswordNow] = React.useState("");
    const [password, setPassword] = React.useState("");
    const [repassword, setRePassword] = React.useState("");
    const [isShowPassword, setIsShowPassword] = React.useState(false);
    const toggleIsShowPassword = () => setIsShowPassword(!isShowPassword);

    const handleSubmit = async (e) => {

        try {

            e.preventDefault();

            if (password === "" || passwordnow === "" || repassword === "") {
                toastService.error("Please enter required inputs");
                return false;
            }

            if (password !== repassword) {
                toastService.error('Password doesn\'t match');
                return false;
            }

            setLoading(true);

            let data = {
                uuid: authRedux?.me?.uuid,
                passwordnow: passwordnow,
                password,
                password_confirmation: repassword,
            }

            let result = await authRepository.changePassword(data);

            if (!result.error) {
                setPasswordNow("");
                setPassword("");
                setRePassword("");
                dispatch(me());
            }

        } catch (error) {
            toastService.error(error);
        } finally {
            setLoading(false);
        }
    }

    return (
        <Box display="flex" flexDirection="row" justifyContent="center" alignItems="center" sx={{ width: '100%', minHeight: 400,  backgroundColor: "#eee" }}>
            <Paper square sx={{ p: 4, m: 5, width: '50%' }}>
                <form onSubmit={(e) => handleSubmit(e)} noValidate autoComplete="off">
                    <Box sx={{ mt: -1 }}>
                        <TextField
                            type={isShowPassword ? "text" : "password"}
                            onChange={(e) => { setPasswordNow(e.target.value); }}
                            value={passwordnow}
                            variant="outlined"
                            size="small"
                            margin="normal"
                            required
                            fullWidth
                            name="password"
                            label="Current Password"
                            placeholder="Current Password"
                            id="password"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <IconButton onClick={toggleIsShowPassword} size="small" sx={{ mr: -0.5 }}>
                                            {isShowPassword ?
                                                <MdVisibility size={15} />
                                                :
                                                <MdVisibilityOff size={15} />
                                            }
                                        </IconButton>
                                    </InputAdornment>
                                ),
                            }}
                        />
                    </Box>
                    <Box sx={{ mt: -1 }}>
                        <TextField
                            type={isShowPassword ? "text" : "password"}
                            onChange={(e) => { setPassword(e.target.value); }}
                            value={password}
                            variant="outlined"
                            size="small"
                            margin="normal"
                            required
                            fullWidth
                            name="newpassword"
                            label="New Password"
                            placeholder="New Password"
                            id="newpassword"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <IconButton onClick={toggleIsShowPassword} size="small" sx={{ mr: -0.5 }}>
                                            {isShowPassword ?
                                                <MdVisibility size={15} />
                                                :
                                                <MdVisibilityOff size={15} />
                                            }
                                        </IconButton>
                                    </InputAdornment>
                                ),
                            }}
                        />
                    </Box>
                    <Box sx={{ mt: -1 }}>
                        <TextField
                            type={isShowPassword ? "text" : "password"}
                            onChange={(e) => { setRePassword(e.target.value); }}
                            value={repassword}
                            variant="outlined"
                            size="small"
                            margin="normal"
                            required
                            fullWidth
                            name="repassword"
                            label="Re-type Password"
                            placeholder="Re-type Password"
                            id="repassword"
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <IconButton onClick={toggleIsShowPassword} size="small" sx={{ mr: -0.5 }}>
                                            {isShowPassword ?
                                                <MdVisibility size={15} />
                                                :
                                                <MdVisibilityOff size={15} />
                                            }
                                        </IconButton>
                                    </InputAdornment>
                                ),
                            }}
                        />
                    </Box>
                    <LoadingButton
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        sx={{ mt: 2 }}
                        loading={loading}
                    >
                        <FaEdit size={15} style={{ marginRight: 3 }} />
                        <Typography>Update</Typography>
                    </LoadingButton>
                </form>
            </Paper>
        </Box>
    )
}
