// Services
import apiRequest from '../services/api/apiService';
import responseService from '../services/response/responseService';

// Redux
import { store } from '../redux/store';

// base path
const path = 'files';

const filesRepository = {
    upload: async (params) => {
        try {
            const token = store.getState().auth.value.token;
            let response = await apiRequest({ token: token, multipart: true }).post(`${path}/upload`, params.formData);
            return responseService.handleHTTPResponse(response, false);
        } catch (e) {
            return responseService.handleHTTPError(e.response);
        }
    },
    update: async (params) => {
        try {
            const token = store.getState().auth.value.token;
            let response = await apiRequest({ token: token, multipart: true }).post(`${path}/update`, params.formData);
            return responseService.handleHTTPResponse(response);
        } catch (e) {
            return responseService.handleHTTPError(e.response);
        }
    },
    remove: async (params) => {
        try {
            const token = store.getState().auth.value.token;
            let response = await apiRequest({ token: token }).post(`${path}/remove`, params);
            return responseService.handleHTTPResponse(response);
        } catch (e) {
            return responseService.handleHTTPError(e.response);
        }
    }
}

export default filesRepository;