import React from 'react';
import { useSelector } from 'react-redux';
import { Box, Paper, Typography } from '@mui/material';
import { FaCalendarDay, FaInfoCircle, FaUser } from 'react-icons/fa';
import { format } from 'date-fns';
import utilityService from '../../../../services/utils/utilityServices';

export default function UserCredentialsDetailsPage() {

    const authRedux = useSelector((state) => state.auth.value);
    const me = authRedux?.me; 

    const role = utilityService.capitalizeFirstLetter(me?.role?.role);
    let last_login = new Date(me?.last_login) ?? Date.now();
    let updated_at = new Date(me?.updated_at) ?? Date.now();

    const cards = [
        {
            label: "Role",
            value: role,
            caption: "",
            icon: <FaUser size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
        },
        {
            label: "Last Login",
            value: format(last_login, "MMMM dd, yyyy hh:mm:ss a"),
            caption: "",
            icon: <FaCalendarDay size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
        },
        {
            label: "Last Update",
            value: format(updated_at, "MMMM dd, yyyy h:mm:ss a"),
            caption: "",
            icon: <FaCalendarDay size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
        },
    ]

    return (
        <Box display="flex" flexDirection="column" sx={{ width: '100%', p: 1 }}>
            <Box display="flex" flexDirection="row" sx={{ backgroundColor: '#eee', p: 1, minHeight: 80 }}>
                <Paper elevaiont={2} square sx={{ p: 1, borderRadius: 0, width: '100%' }} >
                    <Box sx={{ m: 0 }}>
                        <Box
                            display="flex"
                            flexGrow={1}
                            flexDirection="column"
                            sx={{
                                mb: 1,
                                width: '100%',
                                backgroundColor: "#00BFA5",
                                color: "#FFF"
                            }}>
                            <Box display="flex" flexDirection="row" justifyContent="flex-start" sx={{ width: "100%", mt: -2 }}>
                                {
                                    cards.map((data, index) => {
                                        return (
                                            <Paper key={`cards-${index}`} elevation={2} sx={{ p: 1, pl: 2, m: 1, mt: 3, width: '100%', height: 70, display: "flex", flexDirection: "row" }}>
                                                <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                                    <Typography variant="body1" fontSize={12}>{data.label}</Typography>
                                                    <Typography variant="span" fontSize={18} fontWeight="bolder" noWrap>{data.value}</Typography>
                                                    <Typography variant="caption" color="secondary" noWrap>
                                                        {data.caption}
                                                    </Typography>
                                                </Box>
                                                <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                                    {data.icon}
                                                </Box>
                                            </Paper>
                                        )
                                    })
                                }
                            </Box>
                        </Box>
                    </Box>
                </Paper>
            </Box>
        </Box>
    )
}
