import React from 'react';
// Services
import toastService from '../../../services/toast/toastService';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
import { getMember } from '../../../redux/async/membersAsync';
// Repository
import addressRepository from '../../../repositories/addressRepository';
// MUI Components
import Grid from '@mui/material/Grid';
import TextField from '@mui/material/TextField';
import FormControlLabel from '@mui/material/FormControlLabel';
import Switch from '@mui/material/Switch';
import LoadingButton from '@mui/lab/LoadingButton';
import Paper from '@mui/material/Paper';
import Typography from '@mui/material/Typography';
import CircularProgress from '@mui/material/CircularProgress';
// Form Components 
import { FaSave } from 'react-icons/fa';
import Zipcode from './fields/Zipcode';

export default function PermanentAddressForm(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux?.role?.id;

    let me = authRedux?.me;
    let uuid = memberRedux?.selectedMember?.user?.uuid;
    let address = null;

    if (memberRedux?.selectedMember?.address[0]?.address_type === "permanent") {
        address = memberRedux?.selectedMember?.address[0];
    } else {
        if (memberRedux?.selectedMember?.address.length > 1) {
            address = memberRedux?.selectedMember?.address[1];
        }
    }

    if (role === 5) {
        if (me?.address[0]?.address_type === "permanent") {
            address = me?.address[0];
        } else {
            if (me?.address.length > 1) {
                address = me?.address[1];
            }
        }
        uuid = me?.user?.uuid;
    }
    
    const [isEdit, setIsEdit] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    // Permanent Address
    const [zipcode, setZipcode] = React.useState(address?.zipcode?.zipcode ?? "");
    const [street, setStreet] = React.useState(address?.streetpurok ?? "");
    const [brgy, setBrgy] = React.useState(address?.barangay ?? "");
    const [city, setCity] = React.useState(address?.zipcode?.area ?? "");
    const [province, setProvince] = React.useState(address?.zipcode?.province ?? "");

    const handleSubmit = async (e) => {
        e.preventDefault();

        try {
            setLoading(true);
            setIsEdit(false);

            let data = {
                id: address.id,
                uuid: uuid,
                zipcode,
                street,
                brgy,
                city,
                province,
                address_type: 'permanent'
            }

            await addressRepository.update(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setIsEdit(true);

        } catch (error) {
            toastService.error(error);
        }
        setLoading(false);
    }


    React.useEffect(() => {

        if (role === 5) {
            dispatch(getMe());
        } else {
            dispatch(getMember(uuid));
        }

    }, [dispatch, uuid, role]);

    return (
        <form onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <Paper elevation={0} square sx={{ p: 1, m: 1 }}>
                <Grid container spacing={2}>
                    <Grid item xs={12} sx={{ mb: 1 }}>
                        <FormControlLabel
                            sx={{ mr: 1 }}
                            control={
                                loading ?
                                    <Typography sx={{ ml: 1 }}>Saving... <CircularProgress size={15} /></Typography>
                                    :
                                    <Switch checked={isEdit} onChange={() => setIsEdit(!isEdit)} size="small" sx={{ ml: 1 }} />}
                            label={loading ? "" : "Edit"}
                            disabled={loading}
                        />
                        {isEdit &&
                            <React.Fragment>
                                <LoadingButton
                                    sx={{
                                        pt: 0,
                                        pb: 0,
                                        width: 75,
                                        '&:hover': {
                                            backgroundColor: 'transparent'
                                        },
                                        '&:before': {
                                            content: "'|'",
                                            ml: -1,
                                            mr: 1,
                                            color: '#000'
                                        },
                                        fontSize: 14,
                                        color: '#1976D2'
                                    }}
                                    type="submit"
                                    fullWidth
                                    variant="text"
                                    color="primary"
                                    loading={loading}
                                >
                                    <FaSave size={14} style={{ marginRight: 5 }} />
                                    Save
                                </LoadingButton>
                            </React.Fragment>
                        }
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            onChange={(e) => setStreet(e.target.value)}
                            value={street}
                            variant="standard"
                            size="small"
                            required={isEdit}
                            fullWidth
                            id="street"
                            label="Street/Purok/Block/Zone"
                            name="street"
                            inputProps={
                                { readOnly: !isEdit }
                            }
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            onChange={(e) => setBrgy(e.target.value)}
                            value={brgy}
                            variant="standard"
                            size="small"
                            required={isEdit}
                            fullWidth
                            id="brgy"
                            label="Barangay"
                            name="brgy"
                            inputProps={
                                { readOnly: !isEdit }
                            }
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <Zipcode variant="standard" zipcode={zipcode} setZipcode={setZipcode} setCity={setCity} setProvince={setProvince} />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            onChange={(e) => setCity(e.target.value)}
                            value={city}
                            variant="standard"
                            size="small"
                            required={isEdit}
                            fullWidth
                            id="city"
                            label="City / Municipality"
                            name="city"
                            inputProps={
                                { readOnly: true }
                            }
                            helperText={loading ? "Loading Data..." : "Read-only field"}
                        />
                    </Grid>
                    <Grid item xs={4}>
                        <TextField
                            onChange={(e) => setProvince(e.target.value)}
                            value={province}
                            variant="standard"
                            size="small"
                            required={isEdit}
                            fullWidth
                            id="province"
                            label="Province"
                            name="province"
                            inputProps={
                                { readOnly: true }
                            }
                            helperText={loading ? "Loading Data..." : "Read-only field"}
                        />
                    </Grid>
                </Grid>
            </Paper>

        </form>
    )
}
