import React from 'react';
// Services
import toastService from '../../../../services/toast/toastService';
// MUI Component
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
import Avatar from '@mui/material/Avatar';
import CircularProgress from '@mui/material/CircularProgress';
// Page components
import WebCamModal from '../../modals/WebCamModal';
// React Icons
import { FaUser, FaRedoAlt } from 'react-icons/fa';
import { MdAddAPhoto, MdAddPhotoAlternate } from 'react-icons/md';


export default function ProfilePhoto(props) {

 
    const [isPhotoSet, setIsPhotoSet] = React.useState(false);
    const [isUploading, setIsUploading] = React.useState(false);
    const [showWebCam, setShowWebCam] = React.useState(false);
    const toggleWebCam = () => setShowWebCam(!showWebCam);

    const handleCamera = async () => {
        toggleWebCam();
    }

    const handleChangePhoto = () => {
        setIsPhotoSet(false);
        if (props.setIsPhotoSet) {
            props.setIsPhotoSet(false);
        }
    }

    const handleCameraPreview = (img) => {
        if (props.setAvatarPreview) {
            props.setAvatarPreview(img);
        }
        setIsPhotoSet(true);
        if (props.setIsPhotoSet) {
            props.setIsPhotoSet(true);
        }
    }

    const handleUpload = async (event) => {
        try {

            setIsUploading(true);
            let img = event.target.files[0];
            let type = img?.type.split("/")[0];
            
            if(type !== "image"){
                toastService.error("Please use image type"); 
                setIsPhotoSet(false);
                return false;
            }

            let file = URL.createObjectURL(img);

            if (props.setAvatarPreview) {
                props.setAvatarPreview(file);
            }

            if (props.setAvatar) {
                props.setAvatar(img);
                setIsUploading(false);
            }
            setIsPhotoSet(true);

            if (props.setIsPhotoSet) {
                props.setIsPhotoSet(true);
            }
        } catch (error) {
            setIsPhotoSet(false);
            setIsUploading(false);
            toastService.error(error);
        }
    }

    return (
        <Box
            display="flex"
            flexDirection="column"
            justifyContent="start"
            alignItems="center"
            sx={{
                height: '100%',
                width: '100%',
            }}
        >
            {showWebCam && <WebCamModal open={showWebCam} toggle={toggleWebCam} setImagePreview={handleCameraPreview} setAvatar={props.setAvatar} />}
            <Box
                display="flex"
                flexDirection="column"
                justifyContent="center"
                alignItems="center"
            >
                <Avatar style={{ width: props.width ?? 200, height: props.height ?? 180, padding: 2 }} src={props.avatarPreview} variant="square" >
                    {!isUploading && props.avatarPreview === null && <FaUser size={100} />}
                    {isUploading && props.avatarPreview !== null && < CircularProgress />}
                </Avatar>
            </Box>
            {!isPhotoSet &&
                <React.Fragment>
                    <Button
                        sx={{ mt: 2, minWidth: 200, borderRadius: 0, }}
                        variant="contained"
                        component="label"
                        size="small"
                        disabled={props.loading}
                    >

                        Select a Photo
                        <MdAddPhotoAlternate style={{ marginLeft: 5 }} />
                        <input
                            type="file"
                            hidden
                            onChange={(e) => handleUpload(e)}
                            accept="images/png, images/jpg, images/jpeg"
                        />
                    </Button>
                    <Button
                        sx={{ mt: 1, minWidth: 200, borderRadius: 0 }}
                        variant="contained"
                        component="label"
                        size="small"
                        disabled={props.loading}
                        onClick={() => handleCamera()}
                    >
                        Take a Photo
                        <MdAddAPhoto style={{ marginLeft: 5 }} />
                    </Button>
                </React.Fragment>
            }
            {
                isPhotoSet &&
                <Button
                    sx={{ mt: 2, minWidth: 200, borderRadius: 0, }}
                    variant="contained"
                    component="label"
                    size="small"
                    onClick={() => handleChangePhoto()}
                >
                    Change
                    <FaRedoAlt style={{ marginLeft: 5 }} />
                </Button>
            }

        </Box>
    )
}
