import React from 'react'
import InnerDialog from '../shared/Dialog/InnerModalDialog'
import WebCam from '../shared/WebCam/WebCam'

export default function WebCamModal(props) {
    return (
        <div>
            <InnerDialog open={props.open} toggle={props.toggle} title="WEBCAM">
                <WebCam setImagePreview={props.setImagePreview} setAvatar={props.setAvatar} toggle={props.toggle} />
            </InnerDialog>
        </div>
    )
}
