import * as React from 'react';
// MUI Components
import Box from '@mui/material/Box';
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
// Page Components
import TabPanel from './components/TabPanel3';
import UserProfilePage from '../pages/user/UserProfilePage';
// react-icons
import { FaUserAlt } from 'react-icons/fa';

export default function UserProfileTabs(props) {

    const options = [
        {
            name: 'Profile',
            icon: <FaUserAlt size={15} style={{ marginRight: 5 }} />,
            content: () => <UserProfilePage {...props} />,
        }
    ];

    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };

    return (
        <Box sx={{ m: 1 }}>
            <Box sx={{ maxWidth: '100%', mt: 0, bgcolor: 'background.paper' }}>
                <Tabs
                    value={value}
                    sx={{ p: 0, pt: 0, borderBottom: '1px solid #ccc', mb: 1 }}
                    TabIndicatorProps={{
                        style: {
                            display: "none"
                        }
                    }}
                >
                    {
                        options.map((data, index) => {
                            return <Tab
                                key={`tabkey-${index}`}
                                sx={{
                                    minWidth: 200,
                                    backgroundColor: (index === value) ? "#00bfa5" : "#FFFFF",
                                    borderTopLeftRadius: 5,
                                    borderTopRightRadius: 5,
                                    border: '1px solid #ccc'
                                }}
                                onClick={() => handleChange(index)}
                                label={
                                    <Typography variant="caption1" color={(index === value) ? "#FFF" : "#000"}>
                                        <Box display="flex">
                                            {data.icon}
                                            {data.name}
                                        </Box>
                                    </Typography>
                                }
                            />

                        })
                    }
                </Tabs>
                {
                    options.map((data, index) => {
                        return <TabPanel height={400} value={value} padright={'0px'} index={index} key={`tabpanelkey-${index}`}>
                            {data.content()}
                        </TabPanel>
                    })
                }
            </Box>
        </Box>
    );
}
