import React from 'react';
// Repositories
import addressRepository from '../../../../repositories/addressRepository';
// services
import toastService from '../../../../services/toast/toastService';
// MUI Component
import TextField from '@mui/material/TextField';
import CircularProgress from '@mui/material/CircularProgress';
import Typography from '@mui/material/Typography';

export default function Zipcode(props) {

    const [zipcode, setZipcode] = React.useState(props.zipcode ?? "");
    const [found, setFound] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    const handleChangeZipcode = async (event) => {
        try {

            if (event.target.value === "") {
                setZipcode("");
                setFound(false);
                if (props.setZipcode) {
                    props.setZipcode("");
                }

                if (props.setCity) {
                    props.setCity("");
                }

                if (props.setProvince) {
                    props.setProvince("");
                }

                if (props.setZipcodeFound) {
                    props.setZipcodeFound(false);
                }

                return false;
            }

            setLoading(true);

            let params = {
                zipcode: event.target.value
            }

            let response = await addressRepository.getZipcodeDetails(params);

            if (!response.error) {

                setZipcode(response.data?.zipcode ?? "");
                setFound(true);
                if (props.setZipcode) {
                    props.setZipcode(response.data?.zipcode ?? "");
                }

                if (props.setCity) {
                    props.setCity(response.data?.area ?? "");
                }

                if (props.setProvince) {
                    props.setProvince(response.data?.province ?? "");
                }

                if (props.setZipcodeFound) {
                    props.setZipcodeFound(true);
                }

            } else {
                props.setZipcodeFound(false);
                setFound(false);
            }

            setLoading(false);
        } catch (error) {
            toastService.error(error);
        }
    }

    const handleEnter = (e) => {
        if (e.key === "Enter") {
            e.preventDefault();
            handleChangeZipcode(e);
        }
    }

    const handleBlur = (e) => {
        if (!found) {
            handleChangeZipcode(e);
        }
    }

    const handleChange = (e) => {
        setZipcode(e.target.value);
        setFound(false);
    }

    return (
        <TextField
            onChange={(e) => handleChange(e)}
            onKeyDownCapture={(e) => handleEnter(e)}
            onBlur={(e) => handleBlur(e)}
            value={zipcode}
            variant={props.variant ?? "outlined"}
            size="small"
            required
            fullWidth
            id="zipcode"
            label="Zipcode"
            placeholder='Type zipcode and press enter'
            name="zipcode"
            InputProps={{
                endAdornment: loading ?
                    <Typography display="flex" flexDirection="row">
                        Searching...<CircularProgress size={15} stlye={{ marginLeft: 2, marginTop: 2 }} />
                    </Typography>
                    : ""
            }}
        />
    )
}
