import { store } from '../redux/store';
// Services
import apiRequest from '../services/api/apiService';
import responseService from '../services/response/responseService';

const path = "member/business";

const businessRepository = {
    post: async (params) => {
        try {
            const token = store.getState().auth.value.token;
            let response = await apiRequest({ token: token }).post(path, params);
            response['params'] = params;
            return responseService.handleHTTPResponse(response);  
        } catch (e) {
            return responseService.handleHTTPError(e.response);
        }
    },
    put: async (params) => {
        try {
            const token = store.getState().auth.value.token;
            let response = await apiRequest({ token: token }).put(`${path}/${params.id}`, params);
            response['params'] = params;
            return responseService.handleHTTPResponse(response);  
        } catch (e) {
            return responseService.handleHTTPError(e.response);
        }
    },
}

export default businessRepository;