import React from 'react';
import utilityService from '../../../../services/utils/utilityServices';
// Redux
import { useSelector } from 'react-redux';
// MUI Components 
import { CircularProgress, Typography, TextField, MenuItem } from '@mui/material';

const EmploymentTypes = (props) => {

    const employmentTypeRedux = useSelector((state) => state.masterfiles.value);
    const [employmentType, setEmploymentType] = React.useState(props.employmentType ?? '');

    const loading = employmentTypeRedux.loading;
    const status = employmentTypeRedux.status;

    let employmentTypes = [];
    let types = [{ value: "", label: "No Types Found" }];
    if (employmentTypeRedux.masterfiles.employmentTypes.length > 0) {
        employmentTypes = employmentTypeRedux.masterfiles.employmentTypes.map((data, index) => {
            return {
                value: data.id,
                label: utilityService.capitalizeFirstLetter(data.type),
                data: data,
                index: index
            }
        });

        types = employmentTypes;
    }


    const handleChange = (value) => {

        setEmploymentType(value);

        if (props.setEmploymentType) {
            props.setEmploymentType(value)
        }
    }

    return (
        <div>
            {!employmentTypeRedux.loading &&
                <TextField
                    select
                    defaultValue=""
                    label="Employment Status"
                    name="employmentType"
                    size="small"
                    variant={props.variant !== null ? props.variant : "outlined"}
                    value={employmentType}
                    onChange={(e) => handleChange(e.target.value)}
                    fullWidth
                    required={!props.isEdit ?? true}
                    disabled={employmentTypeRedux.loading}
                    inputProps={
                        { readOnly: props.isEdit ?? false }
                    }
                    InputProps={{
                        endAdornment: loading &&
                            <Typography sx={{ width: '100%', textAlign: 'right' }}>
                                {status}
                                <CircularProgress size={15} style={{ marginRight: 15, marginLeft: 5 }} />
                            </Typography>
                    }}
                >
                    {types.map((option, index) => (
                        <MenuItem key={`employmentType-${index}`} value={option.value}>
                            {option.label}
                        </MenuItem>
                    ))}
                </TextField>
            }
        </div>
    )
}

export default EmploymentTypes;