import React from 'react'
import Webcam from "react-webcam";
// MUI Components
import Button from '@mui/material/Button';
import Box from '@mui/material/Box';
import Avatar from '@mui/material/Avatar';
// Page Components
import DropdDownButton from '../DropdownButton/DropdownButton';
// react-icons
import { IoMdCamera, IoMdReverseCamera } from 'react-icons/io';
import { FaCheck } from 'react-icons/fa';

export default function WebCam(props) {

    const webcamRef = React.useRef(null);
    const [facingMode, setFacingMode] = React.useState("user");
    const [deviceId, setDeviceId] = React.useState({});
    const [deviceOptions, setDeviceOptions] = React.useState([]);
    const [isCaptured, setIsCaptured] = React.useState(false);

    const [avatarPreview, setAvatarPreview] = React.useState(null);


    const videoConstraints = {
        width: 400,
        height: 300,
        facingMode: facingMode,
        deviceId: deviceId
    }

    const handleDevices = React.useCallback((mediaDevices) => {

        let devices = mediaDevices.filter(({ kind }) => kind === "videoinput");

        let deviceOptions = devices.map(device => {
            return {
                id: device.deviceId,
                label: device.label,
            };
        });

        setDeviceOptions(deviceOptions);

    }, [setDeviceOptions]);

    const capture = React.useCallback(() => {

        const imageSrc = webcamRef.current.getScreenshot();

        setAvatarPreview(imageSrc);

        if (props.setImagePreview) {
            props.setImagePreview(imageSrc);
        }

        if (props.setAvatar) {
            props.setAvatar(imageSrc)
        }

        setIsCaptured(true);
    }, [webcamRef, props]);

    const rotate = React.useCallback(() => {

        if (facingMode === "user") {
            setFacingMode({ exact: "environment" });
        } else {
            setFacingMode("user");
        }

    }, [setFacingMode, facingMode])

    React.useEffect(() => {

        let mounted = false;

        if (!mounted) {
            navigator.mediaDevices.enumerateDevices().then(handleDevices);
        }

        return () => mounted = true;

    }, [handleDevices]);

    return (
        <Box display="flex" flexDirection="column" sx={{ mt: 2 }}>
            {!isCaptured && <React.Fragment>
                <Box sx={{ mb: 2 }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                    <Button
                        variant="outlined"
                        onClick={capture}
                        sx={{
                            borderRadius: 0,
                            mt: 1,
                            mr: 1,
                            flexGrow: 1
                        }}
                    >
                        <IoMdCamera style={{ marginRight: 2 }} />
                        Capture Image
                    </Button>
                    <Button
                        variant="outlined"
                        onClick={rotate}
                        sx={{
                            borderRadius: 0,
                            mt: 1,
                            flexGrow: 1
                        }}
                    >
                        <IoMdReverseCamera style={{ marginRight: 2 }} />
                        Rotate Camera
                    </Button>
                    <DropdDownButton menuOptions={deviceOptions} event={(value) => setDeviceId(value)} />
                </Box>
                <Webcam
                    audio={false}
                    ref={webcamRef}
                    screenshotFormat="image/jpeg"
                    videoConstraints={videoConstraints}
                />
            </React.Fragment>
            }
            {isCaptured &&
                <Box sx={{ width: '100%' }} display="flex" flexDirection="column" justifyContent="center">
                    <Avatar variant='square' src={avatarPreview} sx={{ width: 450, height: 350 }} />
                    <Box sx={{ width: '100%' }} display="flex" flexDirection="row" justifyContent="space-evenly">
                        <Button variant="contained" sx={{ mt: 1, minWidth: 200 }} onClick={() => setIsCaptured(false)}>
                            Re-take <IoMdCamera style={{ marginLeft: 5 }} size={18} />
                        </Button>
                        <Button variant="contained" sx={{ mt: 1, minWidth: 200 }} onClick={() => props.toggle()}>
                            Save <FaCheck style={{ marginLeft: 5 }} size={18} />
                        </Button>
                    </Box>
                </Box>
            }
        </Box>
    )
}
