import React from 'react'
import { isFinite } from 'lodash';

import IconButton from '@mui/material/IconButton';
import TextField from '@mui/material/TextField';
import Menu from '@mui/material/Menu';
import MenuItem from '@mui/material/MenuItem';
import Typography from '@mui/material/Typography';

import { BiChevronDown } from 'react-icons/bi'

function YearOrMonthMenu(props) {

    const [anchorEl, setAnchorEl] = React.useState(null);
    const open = Boolean(anchorEl);

    let plural = false;
    let yearMonth = "Year";

    if (props.yearOrMonth !== "") {
        yearMonth = props.yearOrMonth;
    } else {
        if (props.serviceLength > 1) {
            plural = true;
            yearMonth = "Years";
        } else {
            plural = false;
        }
    }

    const [label, setLabel] = React.useState(plural ? yearMonth : "Year");

    const handleClick = (event) => {
        setAnchorEl(event.currentTarget);
    };

    const handleClose = () => {
        setAnchorEl(null);
    };

    const handleSelect = (value) => {
        let len = parseInt(props.value);
        let label = value;

        // Remove s
        if (isFinite(len) && len === 1) {
            label = label.slice(0, -1);
        }

        handleClose();

        setLabel(value);
        if (props.setYearOrMonth) {
            props.setYearOrMonth(label);
        }
    }

    return (
        <div>
            <IconButton
                color="default"
                disableRipple={true}
                size="small"
                onClick={handleClick}
                sx={{ pr: 0, backgroundColor: "transparent", "&:hover": { backgroundColor: "transparent" } }}
            >
                <Typography sx={{ mr: 0.5 }}>{props.yearOrMonth ?? label}</Typography><BiChevronDown />
            </IconButton>
            <Menu
                id="basic-menu"
                anchorEl={anchorEl}
                open={open}
                onClose={handleClose}
                MenuListProps={{
                    'aria-labelledby': 'basic-button',
                }}
            >
                <MenuItem onClick={() => handleSelect("Years")}>Years</MenuItem>
                <MenuItem onClick={() => handleSelect("Months")}>Months</MenuItem>
            </Menu>
        </div>
    );
}

export default function YearOrMonth(props) {

    const [value, setValue] = React.useState(props.value ?? "");

    const handleChange = (value) => {
        if (parseInt(value) > 0) {
            setValue(value);

            if (props.setServiceLength) {
                props.setServiceLength(value);
            }

            if (props.setDormancy) {
                props.setDormancy(value);
            }
        } else {
            setValue("");
        }
    }

    React.useEffect(() => {
        setValue(props.value);
    }, [props.value]);

    return (
        <TextField
            type="number"
            value={value}
            onChange={(e) => handleChange(e.target.value)}
            size={props.size ?? "small"}
            fullWidth={props.fullWidth ?? false}
            label={props.label ?? "Length of Service"}
            required={!props.isEdit ?? true}
            variant={props.variant !== null ? props.variant : "outlined"}
            InputProps={{
                endAdornment: YearOrMonthMenu(props),
            }}
            inputProps={{
                style: { textAlign: 'center' },
                readOnly: props.isEdit ?? false,
                min: 1
            }}
        />
    )
}
