import { store } from '../redux/store';
// Services
import apiRequest from '../services/api/apiService';
import responseService from '../services/response/responseService';

const path = "address";

const addressRepository = {
    update: async (params) => {
        try {
            const token = store.getState().auth.value.token;
            let response = await apiRequest({ token: token }).put(`${path}/update`, params);
            return responseService.handleHTTPResponse(response); 
        } catch (e) {
            return responseService.handleHTTPError(e.response);
        }
    },
    getZipcodeDetails: async (params) => {
        try {
            const token = store.getState().auth.value.token;
            let response = await apiRequest({ token: token }).post(`${path}/search-zipcode`, params);
            return responseService.handleHTTPResponse(response, false);  
        } catch (e) {
            return responseService.handleHTTPError(e.response);
        }
    },
}

export default addressRepository;