import React from 'react'
import config from '../../../config/config';
import Scrollbars from 'react-custom-scrollbars-2';
// Redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
// MUI Components
import Paper from '@mui/material/Paper';
import Box from '@mui/material/Box';
import Button from '@mui/material/Button';
// Page Components
import NoData from '../../components/shared/NoData/NoData';
// react-icons
import { FaUpload } from 'react-icons/fa';
import ImageBox from '../shared/ImageBox/ImageBox';
import UploadFileModal from '../modals/UploadFileModal';
import toastService from '../../../services/toast/toastService';
import alertService from '../../../services/alert/alertService';
import filesRepository from '../../../repositories/filesRepository';

export default function IDPresentedList() {

    const dispatch = useDispatch();
    const fileType = 2;
    let allowed = ["edit", "download", "expand", "delete", "upload"];
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);
    const role = authRedux?.role.id;
    const uuid = authRedux?.me?.uuid;

    const [isUpload, setIsUpload] = React.useState(false);
    const toggleIsUpload = () => setIsUpload(!isUpload);

    let me = authRedux.me;
    let attachments = memberRedux?.selectedMember?.attachments;


    if (role === 5) {
        attachments = me.attachments;
    }

    if (role !== 5) {
        allowed = ["expand"];
    }

    let list = attachments.filter(data => {
        return data.attachment_type_id === fileType;
    });

    list = Array.from(list).reverse();

    const handleRemoveFile = async (id = '', fileName = '') => {
        try {

            if (fileName === "") {
                toastService.error("Please select the file to remove");
                return false;
            }
            let action = await alertService.confirm({ title: "Remove this file", text: "Are you sure?" });

            if (action.isConfirmed) {

                alertService.process("Deleting a file...");

                let data = {
                    attachment_id: id.toString(),
                    file_name: fileName,
                    uuid: uuid,
                    file_type: fileType.toString()
                }

                let response = await filesRepository.remove(data);

                if (!response.error) {
                    toastService.success(response.message);
                    await dispatch(getMe()).unwrap();
                } else {
                    toastService.error(response.message);
                }
            }

        } catch (e) {
            toastService.error(e.toString());
        }
    }

    return (
        <div>
            {isUpload &&
                <UploadFileModal user={me?.first_name ?? me?.role?.role} uuid={uuid} type={fileType} counter={list.length} title={"Upload New ID"} open={isUpload} toggle={toggleIsUpload} />
            }
            {list.length <= 0 &&
                <Box sx={{ height: 200 }}>
                    <Box sx={{ zIndex: 1 }}>
                        <NoData event={toggleIsUpload} title="No ID Attachment Found" />
                    </Box>
                </Box>
            }
            {list.length > 0 &&
                <Paper elevation={0} sx={{ p: 1, m: 1 }}>
                    {allowed.includes("upload") && list.length < 5 &&
                        <Box sx={{ mb: 1 }}>
                            <Button
                                sx={{
                                    pt: 0.5,
                                    pb: 0.5,
                                    width: 150,
                                    fontSize: 14,
                                }}
                                type="submit"
                                fullWidth
                                variant="contained"
                                color="primary"
                                onClick={toggleIsUpload}
                            >
                                Upload
                                <FaUpload size={13} style={{ marginLeft: 5 }} />
                            </Button>
                        </Box>
                    }
                    <Scrollbars style={{ overflowY: 'hidden', width: '100%', height: 195, backgroundColor: '#eee' }}>
                        <Box
                            display="flex"
                            flexDirection="row"
                            style={{
                                width: '100%',
                                m: 2,
                                height: '100%'
                            }}
                        >
                            {
                                list.map((data, index) => (
                                    <React.Fragment key={`imgBoxID-${index}`}>
                                        <ImageBox
                                            id={data.id}
                                            uuid={uuid}
                                            user={me?.first_name ?? me?.user?.role?.role}
                                            width={170}
                                            height={170}
                                            src={`${config.RESOURCES}users/${data.file_path}/${data.file_name}`}
                                            alt={data.file_name}
                                            removeEvent={() => handleRemoveFile(data.id, data.file_name)}
                                            fileName={data.file_name}
                                            type={fileType}
                                            allowed={allowed}
                                        />
                                    </React.Fragment>
                                ))
                            }
                        </Box>
                    </Scrollbars>
                </Paper>
            }
        </div>
    )
}
