import React from 'react';
import config from '../../../../config/config';
import { format, parseISO, toDate } from 'date-fns';
import { useSelector } from 'react-redux';
import { Box, Paper, Typography, Divider } from '@mui/material';
import MemberProfileTabs from '../../tabs/MemberProfileTabs';
// react-icons  
import {
    FaAt,
    FaBirthdayCake,
    FaCalendarDay,
    FaInfoCircle,
    FaMapMarkerAlt,
    FaPhoneAlt,
    FaUser
} from 'react-icons/fa';
import UserProfileTabs from '../../tabs/UserProfileTabs';
import utilityService from '../../../../services/utils/utilityServices';

export default function UserProfileDetailsPage() {

    const authRedux = useSelector((state) => state.auth.value);

    let me = authRedux?.me ?? null;
    let isBirthday, birthdate, middle_name, branch, gender, email, first_name, last_name, suffix, contact = '';

    if (me) {
        email = me?.email;
        contact = me?.employee?.contact_no;
        isBirthday = format(new Date(me?.employee?.birthdate ?? Date.now()), "MMMM dd") === format(toDate(Date.now()), "MMMM dd");
        birthdate = format(new Date(me?.employee?.birthdate ?? Date.now()), "MMMM dd, yyyy");
        first_name = me?.employee?.first_name;
        last_name = me?.employee?.last_name;
        suffix = me?.employee?.suffix;
        middle_name = me?.employee?.middle_name !== null ? `${me?.employee?.middle_name[0]}.` : '';
        branch = me?.branch;
        gender = me?.employee?.gender.toLowerCase() === "male" || me?.employee?.gender.toLowerCase() === "m" ? 'his' : 'her';
    }

    return (
        <Box display="flex" flexDirection="column" sx={{ width: '100%', p: 1 }}>
            <Box display="flex" flexDirection="row" sx={{ backgroundColor: '#eee', p: 1, minHeight: 55 }}>
                <Paper elevaiont={2} square sx={{ p: 1, borderRadius: 0, width: '100%' }} >
                    <Box sx={{ m: 0 }}>
                        <Box
                            display="flex"
                            flexGrow={1}
                            flexDirection="column"
                            sx={{
                                mb: 1,
                                width: '100%',
                                backgroundColor: "#00BFA5",
                                color: "#FFF"
                            }}>
                            <Typography variant="h6" sx={{ p: 1, pl: 2 }}>
                                <FaInfoCircle size={18} style={{ marginRight: 5, color: '#fff' }} />
                                Quick Overview
                            </Typography>
                            <Box display="flex" flexDirection="row" justifyContent="flex-start" sx={{ width: "100%", mt: -2 }}>
                                <Box sx={{ width: "31.5%", m: 1 }}>
                                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                            <Typography variant="subtitle1">Full Name</Typography>
                                            <Typography variant="subtitle1" fontWeight="bolder" noWrap>{`${first_name ?? ''} ${middle_name ?? ''} ${last_name ?? ''} ${suffix ?? ''}`}</Typography>
                                            {isBirthday &&
                                                <Typography variant="caption" color="secondary" noWrap>
                                                    <FaBirthdayCake /> Today is {gender} birthday
                                                </Typography>
                                            }
                                            {!isBirthday &&
                                                <Typography variant="caption">
                                                    <FaCalendarDay /> Date of Birth: {birthdate ?? '...'}
                                                </Typography>
                                            }
                                        </Box>
                                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                            <FaUser size={50} style={{ color: "#00BFA5", opacity: 0.7 }} />
                                        </Box>
                                    </Paper>
                                </Box>
                                <Box sx={{ width: "31.5%", m: 1 }}>
                                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                            <Typography variant="subtitle1">Contact Info</Typography>
                                            <Typography variant="subtitle1" fontWeight="bolder" noWrap>{'+639' + contact ?? '###-####-###'}</Typography>
                                            <Typography variant="caption" noWrap>
                                                <FaAt /> Email: {email ?? '...'}
                                            </Typography>
                                        </Box>
                                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                            <FaPhoneAlt size={50} style={{ color: "#1976D2", opacity: 0.7 }} />
                                        </Box>
                                    </Paper>
                                </Box>
                                <Box sx={{ width: "31.5%", m: 1 }}>
                                    <Paper elevation={2} sx={{ p: 1, pl: 2, m: 1, width: '100%', display: "flex", flexDirection: "row" }}>
                                        <Box sx={{ width: '75%', borderRight: '1px solid #eee' }}>
                                            <Typography variant="subtitle1">Branch</Typography>
                                            <Typography variant="subtitle1" fontWeight="bolder" noWrap> {utilityService.capitalizeFirstLetter(branch?.branch) ?? '...'}</Typography>
                                            <Typography variant="caption" noWrap>
                                                <FaMapMarkerAlt /> Location: {utilityService.capitalizeFirstLetter(branch?.address) ?? '...'}
                                            </Typography>
                                        </Box>
                                        <Box sx={{ width: '25%' }} display="flex" flexDirection="row" justifyContent="center" alignItems="center">
                                            <FaMapMarkerAlt size={50} style={{ opacity: 0.7, color: '#E45570' }} />
                                        </Box>
                                    </Paper>
                                </Box>
                            </Box>
                        </Box>
                    </Box>
                </Paper>
            </Box>
            <Divider />
            <Box display="flex" flexDirection="row" sx={{ width: '100%', backgroundColor: '#fff', mt: 1 }}>
                <Box display="flex" flexDirection="column" flexGrow={1} sx={{ minHeight: '100vh' }}>
                    <Paper sx={{ p: 2, m: 0, borderRadius: 0, width: '100%' }}>
                        <UserProfileTabs />
                    </Paper>
                </Box>
            </Box>
        </Box>
    )
}
