import React from 'react'
// Modal Components
import { Button, Dialog, DialogContent } from '@mui/material'
import { FaTimes } from 'react-icons/fa'


export default function ExpandImageModal(props) {

    return (
        <div>
            <Dialog
                open={props.open}
                onClose={props.toggle}
                fullWidth={false}
                fullScreen={false}
                sx={{
                    zIndex: 90,
                    left: 50, 
                    '.MuiDialog-paper': {
                        background: 'transparent',
                        boxShadow: 'none'
                    }
                }}
            >
                <DialogContent>
                    <img src={props.src} alt={props.alt} width={props.width ?? 500} height={props.height ?? 500} />
                </DialogContent>
            </Dialog>
        </div >
    )
}
