import React from 'react';

// MUI Components
import TextField from '@mui/material/TextField';
import MenuItem from '@mui/material/MenuItem';
import { FaUserFriends } from 'react-icons/fa';

const YesOrNoSelect = (props) => { 

    const [yesOrNo, setYesOrNo] = React.useState(props.yesOrNo ?? '');

    const yesOrNoOptions = [
        {
            value: true,
            label: "Yes"
        },
        {
            value: false,
            label: "No"
        }
    ];

    const handleChange = (value) => {

        setYesOrNo(value);

        if (props.setYesOrNo) {
            props.setYesOrNo(value)
        }

    }

    React.useEffect(() => {
        setYesOrNo(props.yesOrNo);
    }, [props.yesOrNo])

    return (
        <div>
            <TextField
                select
                label={props.label ?? "Yes or No?"}
                name="yesOrNo"
                size={props.size ?? "small"}
                variant={props.variant !== null ? props.variant : "outlined"}
                value={yesOrNo}
                onChange={(e) => handleChange(e.target.value)}
                fullWidth
                required
                InputProps={{
                    startAdornment: props.withAdornment ? <FaUserFriends size={22} style={{ marginRight: 10, color: "#00BFA5" }} /> : ""
                }}
                inputProps={{ MenuProps: { disableScrollLock: true } }}
            >
                {yesOrNoOptions.map((option) => (
                    <MenuItem key={option.value} value={option.value}>
                        {option.label}
                    </MenuItem>
                ))}
            </TextField>

        </div>
    )
}

export default YesOrNoSelect;