import React from 'react';  
// redux
import { useDispatch } from 'react-redux';
import { me } from '../../../../redux/async/authAsync';
// services
import toastService from '../../../../services/toast/toastService';
import filesRepository from '../../../../repositories/filesRepository';
// mui components
import { Box, Paper, Button } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// page Components
import InnerDialog from '../Dialog/InnerModalDialog';
import FileDropzone from '../FileDropzone/FileDropzone';
// react-icons
import { FaRedoAlt, FaRegEdit, FaTimes } from 'react-icons/fa';

export default function EditImageModal(props) {

    const [isEdit, setIsEdit] = React.useState(false);
    const toggleIsEdit = () => setIsEdit(!isEdit);
    const dispatch = useDispatch();
    const [files, setFiles] = React.useState([]);
    const [filesPreview, setFilesPreview] = React.useState([]);
    const [disableDrop, setDisableDrop] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    const handleEdit = async () => {
        try {

            setLoading(true);
            setDisableDrop(true);

            let formData = new FormData();

            formData.append("attachment_id", props?.id);
            formData.append("file_name", props?.fileName);
            formData.append("user", props?.user?.toLowerCase());
            formData.append("file_type", props?.type);
            formData.append("uuid", props?.uuid);

            for (let value of files) {
                formData.append('files[]', value);
            }

            let response = await filesRepository.update({ formData });

            if (!response.error) {
                toastService.success(response.message);
                setFiles([]);
                setFilesPreview([]);
                await dispatch(me()).unwrap();
                if (props.toggle) {
                    props.toggle();
                }
            } else {
                toastService.error(response.message);
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false);
            setDisableDrop(false);
        }
    }

    return (
        <InnerDialog open={props.open} toggle={props.toggle} title="Edit File">
            <Box sx={{ width: props.width ?? 500, height: props.height ?? 260, mt: 2 }} display="flex" justifyContent="center">
                {!isEdit &&
                    <Paper
                        width={props.width ?? 500}
                        height={props.height ?? 200}
                        sx={{ m: 1, p: 2 }}
                    >
                        <img
                            width={props.width ?? 220}
                            height={props.height ?? 220}
                            src={props.src ?? ''}
                            alt={props.alt ?? 'Image'}
                            loading="lazy"
                            style={{ cursor: 'pointer' }}
                        />
                    </Paper>
                }
                {isEdit &&
                    <FileDropzone
                        disable={disableDrop}
                        drop={setFiles} 
                        files={files}
                        filesPreview={filesPreview}
                        maxFiles={1}
                        width={props.width ?? 500}
                        height={props.height ?? 200}
                        title="Select or drop new file here"
                        allowClear={false}
                    />
                }
            </Box>
            <Box display="flex" justifyContent="center">
                {!isEdit &&
                    <Button variant="contained" onClick={toggleIsEdit} sx={{ minWidth: 150, mt: 2 }}>
                        <FaRedoAlt style={{ marginRight: 5 }} size={14} />
                        Change
                    </Button>
                }
                {isEdit &&
                    <React.Fragment>
                        <LoadingButton loading={loading} variant="contained" sx={{ minWidth: 150 }} onClick={handleEdit}>
                            <FaRegEdit style={{ marginRight: 5 }} size={14} />
                            Edit
                        </LoadingButton>
                        <LoadingButton loading={loading} onClick={toggleIsEdit} variant="contained" color='secondary' sx={{ minWidth: 150, ml: 1 }}>
                            <FaTimes style={{ marginRight: 5 }} size={14} />
                            Cancel
                        </LoadingButton>
                    </React.Fragment>
                }
            </Box>
        </InnerDialog>
    )
}
