import React, { useRef } from 'react';
import { format, toDate, parseISO } from 'date-fns';
// redux
import { useDispatch, useSelector } from 'react-redux';
import { me as getMe } from '../../../redux/async/authAsync';
import { getMember } from '../../../redux/async/membersAsync';
// repository
import familyRepository from '../../../repositories/familyRepository';
// Services
import toastService from '../../../services/toast/toastService';
// MUI Components
import Grid from '@mui/material/Grid';
import Box from '@mui/material/Box';
import TextField from '@mui/material/TextField';
import LoadingButton from '@mui/lab/LoadingButton';
// Page Component
import Birthdate from './fields/Birthdate';
import Relations from './fields/Relations';
import YearOrMonth from './fields/YearOrMonth';
import ContactNumber from './fields/ContactNumber';
import YesOrNoSelect from './fields/YesOrNoSelect';

export default function EditFamilyMembersForm(props) {

    const dispatch = useDispatch();
    const authRedux = useSelector((state) => state.auth.value);
    const memberRedux = useSelector((state) => state.members.value);

    const role = authRedux.role.id;
    const me = authRedux?.me;
    let uuid = memberRedux?.selectedMember?.user?.uuid;

    const bdate = Object.keys(props.rowData).length > 0 ? parseISO(props?.rowData?.birthdate) : null;

    let beneficiary = false;
    if (props.rowData.isBeneficiary === 1) {
        beneficiary = true;
    }

    if (role === 5) {
        uuid = me?.uuid;
    }

    const formRef = useRef();

    const [loading, setLoading] = React.useState(false);

    const [id, setId] = React.useState("");
    const [name, setName] = React.useState("");
    const [birthdate, setBirthdate] = React.useState(bdate);
    const [relations, setRelations] = React.useState("");
    const [selectedRelations, setSelectedRelations] = React.useState("");
    const [isBeneficiary, setIsBeneficiary] = React.useState(beneficiary);
    const [spouseOccupation, setSpouseOccupation] = React.useState("");
    const [spouseContactNumber, setSpouseContactNumber] = React.useState("");
    const [spouseLengthInService, setSpouseLengthInService] = React.useState("");
    const [spouseYearOrMonth, setSpouseYearOrMonth] = React.useState("");

    const handleSelectedRelations = (relation) => {
        setSelectedRelations(relation);
    }

    const handleStore = async () => {
        try {
            setLoading(true);

            let bdate = format(toDate(birthdate), "Y-M-d");

            let data = {
                uuid,
                name,
                birthdate: bdate,
                relation: relations,
                isBeneficiary,
            }

            if (relations.toLowerCase() === "spouse") {
                data = {
                    uuid,
                    name,
                    birthdate: bdate,
                    relation: relations,
                    isBeneficiary,
                    spouseOccupation,
                    spouseContactNumber,
                    spouseLengthInService,
                    spouseYearOrMonth,
                }

            }


            await familyRepository.post(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setLoading(false);

            props.toggle();

        } catch (error) {
            setLoading(false);
            toastService.error(error)
        }
    }

    const handleUpdate = async () => {
        try {
            setLoading(true);

            let bdate = format(toDate(birthdate), "Y-M-d");

            let data = {
                id,
                uuid,
                name,
                birthdate: bdate,
                relation: relations,
                isBeneficiary,
            }

            await familyRepository.put(data);

            if (role === 5) {
                dispatch(getMe());
            } else {
                dispatch(getMember(data.uuid));
            }

            setLoading(false);

            props.toggle();

        } catch (error) {
            setLoading(false);
            toastService.error(error)
        }
    }

    const handleSubmit = (e) => {
        e.preventDefault();

        if (props.action.toLowerCase() === "edit") {
            handleUpdate();
        } else {
            handleStore();
        }

    }

    const handleChangeLengthInService = (value) => {

        setSpouseYearOrMonth("");

        let serviceLength = parseInt(value);
        let label = "Month";

        
        if (spouseYearOrMonth !== "") {
            if (spouseYearOrMonth === "Year" || spouseYearOrMonth === "Years") {
                label = "Year";
            } 
        }

        // Checking if plural or not
        if (serviceLength > 1) {
            label = `${label}s`;
        }

        setSpouseLengthInService(value);
        setSpouseYearOrMonth(label);

    }

    React.useEffect(() => {
        let mounted = false;

        if (role === 5) {
            dispatch(getMe());
        } else {
            dispatch(getMember(uuid));
        }

        if (!mounted) {
            if (Object.keys(props.rowData).length > 0) {
                setId(props.rowData.id ?? "");
                setSelectedRelations(props.rowData?.relation?.toLowerCase() ?? "");
                setRelations(props.rowData?.relation?.toLowerCase() ?? "");
                setName(props.rowData.name ?? "");
                setSpouseOccupation(props.rowData?.spouse?.occupation ?? "");
                setSpouseContactNumber(props.rowData?.spouse?.contact_no ?? "");
                let serviceDetails = props.rowData?.spouse?.length_of_service.split(" ");
                if (serviceDetails.length >= 0) {
                    setSpouseLengthInService(serviceDetails[0] ?? "");
                    setSpouseYearOrMonth(serviceDetails[1] ?? "");
                }

            }
        }

        return () => mounted = true;
    }, [dispatch, uuid, role, props.rowData]);

    return (
        <form ref={formRef} onSubmit={(e) => handleSubmit(e)} autoComplete="off">
            <Grid container spacing={2}>
                <Grid item xs={6} sx={{ mt: 2 }}>
                    <YesOrNoSelect yesOrNo={isBeneficiary} setYesOrNo={setIsBeneficiary} label="Is he/she your beneficiary?" />
                </Grid>
                <Grid item xs={6} sx={{ mt: 2 }}>
                    <Relations setSelectedRelations={(relation) => handleSelectedRelations(relation)} selectedRelations={selectedRelations} relations={relations} setRelations={setRelations} />
                </Grid>
                <Grid item xs={12}>
                    <TextField
                        onChange={(e) => setName(e.target.value)}
                        value={name}
                        variant="outlined"
                        size="small"
                        required
                        fullWidth
                        id="fullname"
                        label="Full Name"
                        name="fullname"
                    />
                </Grid>
                <Grid item xs={relations === "spouse" ? 6 : 12}>
                    <Birthdate setBirthdate={setBirthdate} birthdate={birthdate} variant="outlined" />
                </Grid>
                {
                    relations.toLowerCase() === "spouse" &&
                    <React.Fragment>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseContactNumber(e.target.value)}
                                value={spouseContactNumber}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="spouseContactNumber"
                                label="Spouse Mobile No."
                                name="spouseContactNumber"
                                InputProps={{
                                    inputComponent: ContactNumber
                                }}
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <TextField
                                onChange={(e) => setSpouseOccupation(e.target.value)}
                                value={spouseOccupation}
                                variant="outlined"
                                size="small"
                                required
                                fullWidth
                                id="spouseOccupation"
                                label="Spouse Occupation"
                                name="spouseOccupation"
                            />
                        </Grid>
                        <Grid item xs={6}>
                            <YearOrMonth
                                fullWidth={true}
                                value={spouseLengthInService}
                                setServiceLength={(value) => handleChangeLengthInService(value)}
                                yearOrMonth={spouseYearOrMonth}
                                setYearOrMonth={setSpouseYearOrMonth}
                            />
                        </Grid>
                    </React.Fragment>
                }
            </Grid>
            <Box display="flex" flexDirection="row" justifyContent="flex-end" sx={{ mt: 2 }}>
                <LoadingButton
                    sx={{ width: 150 }}
                    type="submit"
                    fullWidth
                    variant="contained"
                    color="primary"
                    loading={loading}
                >
                    {props.action.toLowerCase() === "edit" ? "Save" : "Add"}
                </LoadingButton>
                <LoadingButton
                    sx={{ width: 150, ml: 1 }}
                    type="button"
                    fullWidth
                    variant="contained"
                    color="secondary"
                    onClick={() => props.toggle()}
                    loading={loading}
                >
                    Cancel
                </LoadingButton>
            </Box>
        </form>
    )
}
