import React from 'react';
// redux
import { useSelector } from 'react-redux';
// MUI Components  
import TextField from "@mui/material/TextField";
import Box from "@mui/material/Box";
import Typography from "@mui/material/Typography";
import Paper from "@mui/material/Paper";
import InputAdornment from "@mui/material/InputAdornment";
import Button from '@mui/material/Button';
import LoadingButton from '@mui/lab/LoadingButton';

// react-icons
import { MdEmail, MdAttachEmail } from 'react-icons/md';
import { FaEdit, FaArrowLeft, FaCheckCircle } from 'react-icons/fa';
import toastService from '../../../services/toast/toastService';
import usersRepository from '../../../repositories/usersRepository';

export default function ChangeEmailForm() {

    const authRedux = useSelector((state) => state.auth.value);

    const role = authRedux.role.id;
    let myEmail = authRedux?.me?.email;
    if (role === 5) {
        myEmail = authRedux?.me?.user?.email
    }



    const [currentEmail, setCurrentEmail] = React.useState(myEmail ?? "");
    const [email, setEmail] = React.useState("");
    const [code, setCode] = React.useState("");

    const [steps, setSteps] = React.useState(0);
    const [loading, setLoading] = React.useState(false);

    const handleSendCode = async (e) => {

        try {

            e.preventDefault();

            if (email === "" || email === currentEmail) {
                toastService.error("Please set your new email address");
                return false;
            }

            let data = {
                email
            }

            setLoading(true);

            let result = await usersRepository.sendCode(data);
            if (!result.error) {
                setSteps(steps + 1);
            }
        } catch (error) {
            toastService.error(error)
        } finally {
            setLoading(false);
        }

    }

    const handleSubmit = async (e) => {

        try {

            e.preventDefault();

            let data = {
                'uuid': authRedux?.me?.uuid,
                'currentEmail': authRedux?.me?.user?.email,
                'email': email,
                'code': code
            }

            setLoading(true);

            let result = await usersRepository.changeEmail(data);

            if (!result.error) {
                setCurrentEmail(email);
                setSteps(0);
            }

        } catch (error) {
            toastService.error(error)
        } finally {
            setLoading(false);
        }


    }


    return (
        <Box display="flex" flexDirection="row" justifyContent="center" alignItems="center" sx={{ width: '100%', minHeight: 400, backgroundColor: "#eee" }}>
            {steps === 0 &&
                <Paper square sx={{ p: 4, m: 5, width: '50%' }}>
                    <Box>
                        <TextField
                            defaultValue={currentEmail}
                            variant="outlined"
                            size="small"
                            margin="normal"
                            required
                            fullWidth
                            name="email"
                            id="email"
                            label="Email Address"
                            placeholder="Email Address"
                            autoFocus
                            InputProps={{
                                endAdornment: (
                                    <InputAdornment position="end">
                                        <MdEmail />
                                    </InputAdornment>
                                ),
                            }}
                            inputProps={{
                                readOnly: true
                            }}
                        />
                    </Box>
                    <Button
                        type="submit"
                        fullWidth
                        variant="contained"
                        color="primary"
                        sx={{ mt: 2 }}
                        onClick={() => setSteps(steps + 1)}
                    >
                        <FaEdit size={15} style={{ marginRight: 3 }} />
                        <Typography>Change</Typography>
                    </Button>
                </Paper>
            }
            {steps === 1 &&
                <Paper square sx={{ p: 4, m: 5, width: '50%' }}>
                    <Button onClick={() => setSteps(steps - 1)}><FaArrowLeft style={{ marginRight: 5 }} /> Back</Button>
                    <form onSubmit={(e) => handleSendCode(e)} noValidate autoComplete="off">
                        <Box>
                            <TextField
                                onChange={(e) => setEmail(e.target.value)}
                                value={email}
                                variant="outlined"
                                size="small"
                                margin="normal"
                                required
                                fullWidth
                                name="email"
                                id="email"
                                label="New Email"
                                placeholder="New Email"
                                autoFocus
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <MdEmail />
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </Box>
                        <LoadingButton
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            sx={{ mt: 2 }}
                            loading={loading}
                        >
                            <MdAttachEmail size={15} style={{ marginRight: 3 }} />
                            <Typography>Send Code</Typography>
                        </LoadingButton>
                    </form>
                </Paper>
            }
            {steps === 2 &&
                <Paper square sx={{ p: 4, m: 5, width: '50%' }}>
                    <Button onClick={() => setSteps(steps - 1)}><FaArrowLeft style={{ marginRight: 5 }} /> Back</Button>
                    <form onSubmit={(e) => handleSubmit(e)} noValidate autoComplete="off">
                        <Typography>
                            We've sent a verification code on your new email address.
                        </Typography>
                        <Box>
                            <TextField
                                onChange={(e) => setCode(e.target.value)}
                                value={code}
                                variant="outlined"
                                size="small"
                                margin="normal"
                                required
                                fullWidth
                                name="code"
                                id="code"
                                label="Verification Code"
                                placeholder="Verification Code"
                                autoFocus
                                InputProps={{
                                    endAdornment: (
                                        <InputAdornment position="end">
                                            <MdEmail />
                                        </InputAdornment>
                                    ),
                                }}
                            />
                        </Box>
                        <LoadingButton
                            type="submit"
                            fullWidth
                            variant="contained"
                            color="primary"
                            sx={{ mt: 2 }}
                            loading={loading}
                        >
                            <Typography>Submit</Typography>
                            <FaCheckCircle size={15} style={{ marginLeft: 5 }} />
                        </LoadingButton>
                    </form>
                </Paper>
            }
        </Box>
    )
}
