import React from 'react';
// redux
import { useDispatch } from 'react-redux';
import { me } from '../../../redux/async/authAsync';
// services
import toastService from '../../../services/toast/toastService';
import filesRepository from '../../../repositories/filesRepository';
// mui components
import { Box } from '@mui/material';
import { LoadingButton } from '@mui/lab';
// page components
import FileDropzone from '../shared/FileDropzone/FileDropzone';
import InnerModalDialog from '../shared/Dialog/InnerModalDialog';
// react-icons
import { FaTimes, FaUpload } from 'react-icons/fa';


export default function UploadFileModal(props) {

    const dispatch = useDispatch();
    const [files, setFiles] = React.useState([]);
    const [filesPreview, setFilesPreview] = React.useState([]);
    const [disableDrop, setDisableDrop] = React.useState(false);
    const [loading, setLoading] = React.useState(false);

    const handleUpload = async () => {

        try {

            setLoading(true);
            setDisableDrop(true);

            let formData = new FormData();

            formData.append("attachment_id", props.id);
            formData.append("file_name", `${props.counter}_${props.user.toLowerCase()}`)
            formData.append("file_type", props.type);
            formData.append("uuid", props.uuid);

            for (let value of files) {
                formData.append('files[]', value);
            }

            let response = await filesRepository.upload({ formData });

            if (!response.error) {
                toastService.success(response.message);
                setFiles([]);
                setFilesPreview([]);
                await dispatch(me()).unwrap();
                if (props.toggle) {
                    props.toggle();
                }
            } else {
                toastService.error(response.message);
            }

        } catch (e) {
            toastService.error(e.toString());
        } finally {
            setLoading(false);
            setDisableDrop(false);
        }

    }

    return (
        <InnerModalDialog title={props.title ?? "New Files"} open={props.open} toggle={props.toggle}>
            <Box sx={{ width: props.width ?? 500, height: props.height ?? 260, mt: 2 }}>
                <FileDropzone
                    disable={disableDrop}
                    maxFiles={props.maxFiles ?? 1}
                    drop={setFiles}
                    iles={files}
                    filesPreview={filesPreview}
                    title={props.dropTitle ?? "Select or drop files here"}
                    width={props.width ?? 500} height={props.height ?? 200}
                    allowClear={false}
                />
                <Box display="flex" flexDirection="row" justifyContent="center" sx={{ mt: 2 }}>
                    <LoadingButton loading={loading} variant="contained" onClick={handleUpload}>
                        Upload <FaUpload size={14} style={{ marginLeft: 5 }} />
                    </LoadingButton>
                    <LoadingButton loading={loading} onClick={props.toggle} variant="contained" color="secondary" sx={{ ml: 1 }}>
                        Cancel <FaTimes size={14} style={{ marginLeft: 5 }} />
                    </LoadingButton>
                </Box>
            </Box>
        </InnerModalDialog>
    )
}
