import * as React from 'react';
// MUI Components 
import Tabs from '@mui/material/Tabs';
import Tab from '@mui/material/Tab';
import Typography from '@mui/material/Typography';
import Box from '@mui/material/Box';
import Paper from '@mui/material/Paper';
// Page Components
import TabPanel from './components/TabPanel2';
import ChangePasswordForm from '../forms/ChangePasswordForm';
import ChangeEmailForm from '../forms/ChangeEmailForm';
// react-icons
import { FaUserLock, FaEnvelope } from 'react-icons/fa';

export default function CredentialTabs(props) {

    const options = [
        {
            name: 'Change Password',
            icon: <FaUserLock size={20} style={{ marginRight: 5 }} />,
            content: () => <ChangePasswordForm  {...props} />,
        },
        {
            name: 'Change Email',
            icon: <FaEnvelope size={20} style={{ marginRight: 5 }} />,
            content: (props) => <ChangeEmailForm {...props} />,
        },
    ];

    const [value, setValue] = React.useState(0);

    const handleChange = (newValue) => {
        setValue(newValue);
    };


    return (
        <Box sx={{ m: 1 }}>
            <Box sx={{ maxWidth: '100%', mt: 0, bgcolor: 'background.paper' }}>
                <Tabs
                    value={value}
                    sx={{ p: 0, pt: 0, borderBottom: '1px solid #ccc', mb: 1 }}
                    TabIndicatorProps={{
                        style: {
                            display: "none"
                        }
                    }}
                >
                    {
                        options.map((data, index) => {
                            return <Tab
                                key={`tabkey-${index}`}
                                sx={{
                                    minWidth: 200,
                                    backgroundColor: (index === value) ? "#00bfa5" : "#FFFFF",
                                    borderTopLeftRadius: 5,
                                    borderTopRightRadius: 5,
                                    border: '1px solid #ccc'
                                }}
                                onClick={() => handleChange(index)}
                                label={
                                    <Typography variant="caption1" color={(index === value) ? "#FFF" : "#000"}>
                                        <Box display="flex">
                                            {data.icon}
                                            {data.name}
                                        </Box>
                                    </Typography>
                                }
                            />

                        })
                    }
                </Tabs>
                {
                    options.map((data, index) => {
                        return <TabPanel height={400} value={value} padright={'0px'} index={index} key={`tabpanelkey-${index}`}>
                            {data.content()}
                        </TabPanel>
                    })
                }
            </Box>
        </Box>
    );
}
